body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  font-family: "Times New Roman", Times, serif;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

html,
.mainDiv {
  overflow-x: hidden;
  height: 200%;
  background: #201654; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(
    linear,
    left top, right top,
    from(#201654),
    to(#95188c)
  );
  background: linear-gradient(
    to right,
    #201654,
    #95188c
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
@-webkit-keyframes fadeOut {
  from {
    opacity: 100%;
  }
  to {
    opacity: 0%;
  }
}
@keyframes fadeOut {
  from {
    opacity: 100%;
  }
  to {
    opacity: 0%;
  }
}

@-webkit-keyframes cfadeOut {
  from {
    opacity: 70%;
  }
  to {
    opacity: 0%;
  }
}

@keyframes cfadeOut {
  from {
    opacity: 70%;
  }
  to {
    opacity: 0%;
  }
}

.fade-out {
  opacity: 100%;
  -webkit-animation-name: fadeOut;
          animation-name: fadeOut;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0%;
  }

  100% {
    opacity: 100%;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0%;
  }

  100% {
    opacity: 100%;
  }
}

@-webkit-keyframes cfadeIn {
  0% {
    opacity: 0%;
  }

  100% {
    opacity: 70%;
  }
}

@keyframes cfadeIn {
  0% {
    opacity: 0%;
  }

  100% {
    opacity: 70%;
  }
}

@-webkit-keyframes moveUp {
  0% {
  }

  50% {
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
  }
  100% {
    -webkit-transform: translateY(-40%);
            transform: translateY(-40%);
  }
}

@keyframes moveUp {
  0% {
  }

  50% {
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
  }
  100% {
    -webkit-transform: translateY(-40%);
            transform: translateY(-40%);
  }
}

.fade-in {
  opacity: 0%;
  -webkit-animation-name: fadeIn;
          animation-name: fadeIn;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.card-fade-in {
  opacity: 100%;
  background-color: ivory;
  -webkit-animation-name: fadeIn;
          animation-name: fadeIn;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.card1-fade-in {
  opacity: 0%;
  background-color: #95188c;
  -webkit-animation-name: cfadeIn;
          animation-name: cfadeIn;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
.card1-fade-out {
  opacity: 70%;
  background-color: #95188c;
  -webkit-animation-name: cfadeOut;
          animation-name: cfadeOut;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
.card-move-up {
  -webkit-animation-name: moveUp;
          animation-name: moveUp;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  position: fixed;
  width: 100%;
  z-index: 100;
  background-color: #20165400;
}

.opaque {
  background-color: #20165400;
  -webkit-animation-name: navAppear;
          animation-name: navAppear;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes navAppear {
  0% {
    background-color: #20165400;
  }

  100% {
    background-color: #201654;
  }
}

@keyframes navAppear {
  0% {
    background-color: #20165400;
  }

  100% {
    background-color: #201654;
  }
}

@-webkit-keyframes navDisappear {
  0% {
    background-color: #201654;
  }

  100% {
    background-color: #20165400;
  }
}

@keyframes navDisappear {
  0% {
    background-color: #201654;
  }

  100% {
    background-color: #20165400;
  }
}
.transparent {
  background-color: #201654;

  -webkit-animation-name: navDisappear;

          animation-name: navDisappear;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

ul:hover {
  opacity: 100%;
}
li {
  float: left;
}

li a {
  display: block;
  color: white;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

li a:hover {
  background-color: ivory;
}

.footer {
  position: absolute;
  top: 200%;
  left: 0;
  bottom: 0%;
  width: 100%;
  padding-bottom: 4%;
  padding-top: 1%;
  margin-bottom: 0;
  background-color: #0a003f70;
  color: white;
  text-align: center;
}

.text {
  font-family: "Times New Roman", Times, serif;
  color: ivory;
  font-size: xx-large;
  margin-bottom: 15%;
}

